.form {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

input[type=text], input[type=number], textarea {
    width: 100%;
    font-size: 20px;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
}

input[type=text]:focus, input[type=number]:focus, textarea:focus {
    background-color: #ddd;
    outline: none;
 }

 form {
    display: flex;
    flex-direction: column;
    background: #d2dce1;
    padding: 4vh 3vh;
    width: 70%;
  }
  
  label {
    font-family: 'Courier New', Courier, monospace;
  }
 

  h1 {
    font-size: 90px;
  }

  .margin {
    margin-top: 100px;
  }